/* Hero styles */
.hero .dp
{
    border-radius: 50%;
    border: 5px solid #ededee;
    height: 30vh;
    opacity: 0.85;
    width: 30vh;
    -webkit-animation: dp 5s linear 1;
            animation: dp 5s linear 1;
}

.hero .title
{
    -webkit-animation: up 1s linear 1;
            animation: up 1s linear 1;
}
.hero .description
{
    -webkit-animation: up 2s linear 1;
            animation: up 2s linear 1;
}
.hero .social
{
    -webkit-animation: up 3s linear 1;
            animation: up 3s linear 1;
}
@-webkit-keyframes up {
    0%
    {
        opacity: 0;
        -webkit-transform: translateY(20vh);
                transform: translateY(20vh);
    }
    50%
    {
        opacity: 1;
        -webkit-transform: translateY(0vh);
                transform: translateY(0vh);
    }
}
@keyframes up {
    0%
    {
        opacity: 0;
        -webkit-transform: translateY(20vh);
                transform: translateY(20vh);
    }
    50%
    {
        opacity: 1;
        -webkit-transform: translateY(0vh);
                transform: translateY(0vh);
    }
}

@-webkit-keyframes dp {
    0%
    {
        opacity: 0;
    }
    50%
    {
        opacity: 1;
    }
}

@keyframes dp {
    0%
    {
        opacity: 0;
    }
    50%
    {
        opacity: 1;
    }
}

.hero .social
{
    margin:0;
    padding: 0;
}

.hero .title
{
    font-family: cursive;
    letter-spacing: 0.1rem;
    margin: 5px;
    padding: 0;
}

.hero .description
{
    margin: 5px;
    letter-spacing: 0.2rem;
    padding: 0;
}

.hero .social .social-link
{
    border: 0.5px solid #ededee;
    border-radius: 50%;
    display: inline-block;
    font-size: 1rem;
    margin: 5px;
    padding: 0.5rem;
    text-transform: uppercase;
    transition-property: background-color;
    transition-duration: 1s;
    width: 1.5rem;
}

.hero .social .social-link:hover
{
    background-color: #ededee;
    color: #202020;
}
/* Navbar styles */
.navbar .nav
{
    margin: 0;
    padding: 0;
}

.navbar .nav li
{
    display: inline-block;
    padding: 0;
    margin: 5px;
}

.navbar .nav .link
{
    border: 0.5px solid #ededee;
    border-radius: 1rem;
    display: inline-block;
    font-size: 1.2rem;
    font-weight: bold;
    padding: 0.5rem;
    letter-spacing: 0.2rem;
    text-transform: uppercase;
    transition-property: background-color;
    transition-duration: 1s;
    opacity: 0.9;
    width: 5vw;
    text-align: center;
}


.navbar .nav .link:hover,
.navbar .nav .link.active
{
    background-color: #ededee;
    color: #202020;
    opacity: 1;
}

.navbar-top
{
    background-color: rgba(20,20,20,0.8);
    color: #ededee;
    left:0;
    max-width: 100%;
    padding: 2px;
    position: absolute;
    text-align: center;
    top:0;
    width: 100vw;
}

.nav_assist
{
    display: none;
    height: 2rem;
}

@media screen and (max-width:800px),
screen and (max-height:700px) {
    .nav
    {
        display: flex;
        flex-direction: column;
        position: absolute;
        top: 50%;
        left: 0;
        text-align: center;
        -webkit-transform: translateY(-50%);
                transform: translateY(-50%);
    }

    .navbar .nav .link
    {
        font-size: 0.8rem;
    }
}
.skills #skillset
{
    width: 100%;
    height: 100%;
}
.skills .content
{
    border: 1vw solid black;
    color: #000;
    background-color: #e8e8e8;
    padding: 1vh;
    opacity: 0.9;
    max-height: 80vh;
    max-width: 80vw;
    overflow-x: hidden;
    overflow-y: scroll;
}
.projectPlaceHolder
{
    margin-top: 3vh;
    font-size: 2em;
    max-width: 40vw;
}

.projectPlaceHolder .frontIcon
{
    font-size: 3em;
}

.projectPlaceHolder .projects
{
    font-size: 1rem;
    text-align: left;
}
.projects .content
{
    min-height: 50vh;
    min-width: 60vw;
    max-width: 80vw;
    max-height: 80vh;
    border: 1vw solid black;
    color: #000;
    background-color: #e8e8e8;
    padding: 2vh;
    opacity: 0.9;
    overflow-x: hidden;
    overflow-y: auto;
}

.projects .content .container
{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    width: 65vw;
    height: 100%;
    margin-top: 1vw;
}
/* Header styles */
.header
{
    color: #ededee;
    text-align: center;
    height: 100vh;
    max-height: 100vh;
}

.wrapper
{
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    -webkit-animation: show 0.5s linear 1;
            animation: show 0.5s linear 1;
}

@-webkit-keyframes show {
    0%
    {
        opacity: 0;
    }
    100%
    {
        opacity: 1;
    }
}

@keyframes show {
    0%
    {
        opacity: 0;
    }
    100%
    {
        opacity: 1;
    }
}
*
{
    margin:0;
    padding: 0;
}

html,body {
    width: 100%;
    height: 100%;
    margin: 0px;
    padding: 0px;
    overflow-x: hidden;
}

body
{    
    font-family: 'Open Sans', sans-serif;
    font-size: 16px;
    background-color: #1d1d1d;
    background-attachment: fixed;
    background-size: cover;
    background-image: url(/static/media/background.b89092cd.jpg);
}

a
{
    color: inherit;
    font-family: 'Lato', sans-serif;
    outline: none;
    text-decoration: none;
}
